<template>
        <div>
            <div class="jumbotron bg-primary text-light">
                        <div class="container">
                            <b>
                                <h1 class="display-4">
                                    TodoApp
                                </h1>
                                <p class="lead">
                                    Keep track of your daily tasks with ease
                                </p>
                            </b>
                        </div>
            </div>
                <br>
                <br>
            <div class="container">
                <div class="row">
                    <div class="col-md-4 offset-4">
                        <input type="text" id="taskip" v-model="todoItem"
                        @keydown.enter="InserItem" placeholder="Enter Task Here">
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12">
                        <h3>Task List</h3>
                        <table class="table table-striped">
                            <thead class="thead-light">
                                <th>ID</th>
                                <th>Name</th>
                                <th>mark as completed</th>
                                <th>Remove</th>
                            </thead>
                            <tbody>
                                <tr v-for="task , index in todoItems" v-bind:key="task">
                                    <td>{{task.id}}</td>
                                    <td>
                                        <div v-if="!task.editing" @click="taskEdit(task)" v-bind:class="{completed: task.isCompleted}">
                                            {{task.name}}
                                        </div>
                                        <input v-else type="text" v-model="task.name" @keydown.enter="editCompleted(task)" @blur="editCompleted(task)">
                                        
                                     </td>
                                    <td><input v-model="task.isCompleted" type="checkbox"></td>
                                    <td><button @click="remove(index)" class="button btn-sm btn-danger">Remove</button></td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
      </div>      
</template>

<script>


export default {
  data(){
      return {
          todoItem: '',
          // this array will hold all the todos
          todoItems: [],
          id:0,
      }
  },
  methods: {
      InserItem(){
            this.todoItems.push({name:this.todoItem,isCompleted: false ,id:this.id, editing:false })
            this.id++
            this.todoItem = ''
      },

      taskEdit(task){
           console.log("name was clicked")
           task.editing = true
      },
      editCompleted(task){
          task.editing = false
      },

      remove(index){
          this.todoItems.splice(index,1)

      }
  }
}
</script>

